import Vue from 'vue'
import router from '../router'

var bus =new Vue()

export default{
	name:'Login',
	data(){
		return{
			name:'',
			tel:'',
			pwd:'',
			index:0,
			lx:[{name:'公司'},{name:'个人'}]
		}
	},
	methods:{
		handlefh(){
			this.$router.push({path:'/footer/index'})
		},
		handlelx(list,idx){
			this.index=idx
		},
		handlelogin(){
			if(!this.name||!this.tel||!this.pwd){
				alert("不能为空！")
				return
			}
			// 
			var logins={name:this.name,tel:this.tel,pwd:this.pwd}
			// console.log(logins)
			var login=JSON.stringify(logins)
			localStorage.setItem("login",login)
			 this.$router.push({path:'/footer/index'});


			 // bus.$emit("give",this.name)
		}

	}
}



